*{
    margin: 0;
    padding: 0;
    
}
body{
    background-color: rgb(115, 149, 128);
}
.App{
    /* width: 100vh;
    height: 100vh; */
    
    margin-top: 200px;
    display: flex;
    justify-content: center;
    align-content: center; 
    margin: auto;
    min-width: 900px;
    
    
}
.board{
    width: calc(64px*8);
    height: calc(64px*8);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.cell{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cell img{
    width: 48px;
    height: 48px;
    position: relative;
}
.black{
    
    background-color: rgb(177, 157, 231);
}
.white{
    background-color: rgb(0, 255, 225);
    
}
.selected{
    background-color: aqua;
}
.available{
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color:rgba(233, 51, 51, 0.329);
}
.currentPlayerBlack,.currentPlayerWhite,.color__h3{
    font-size: 30px;
    height: 50px;
    margin-bottom: 10px;
    text-align: center;
    
    
}
.currentPlayerWhite{
    color: rgb(222, 245, 240);

}
.currentPlayerBlack{
    color: rgb(0, 0, 0);
}
.color__h3{
    display: flex;
}
.LostFigure{
    height: auto;
    padding: 30px;
    margin-left: 50px;
    background-color: lightgray;
}